import React, { Component } from 'react';
import api from '../../services/api';
import Playlist from '../Playlist/component';
import './style.css';
import { isUndefined } from 'util';

class Busca extends Component {

  constructor(props){
    super(props);
    this.state = {
      palavraChave: '',
      albuns: []
    }
    this.buscaPalavraChave = this.buscaPalavraChave.bind(this);
  }

  buscaPalavraChave(evento){
    let valor = evento.target.value;
    this.setState({
      palavraChave: valor
    });
    const response = api.get(`search?q=${this.state.palavraChave}&type=album`)
      .then((result) => {
        this.setState({
          albuns: result.data.albums.items
        });
        console.log(result.data.albums.items)
      }).catch(erro => console.error(erro));
    console.log(this.state.palavraChave);
  }

  render(){
    return (
      <form>
        <div className="form-group">
          <label>Busque por artistas, albuns ou músicas</label>
          <input type="text" placeholder="Comece buscando aqui..." onChange={this.buscaPalavraChave} value={this.state.palavraChave}></input>
          {this.state.albuns != isUndefined ? <Playlist list={this.state.albuns}/> : ''}
        </div>
      </form>
    )
  }
}

export default Busca;