import React, {Component} from 'react';
import './playlist.css';

class Playlist extends Component {

  constructor(props){
    super(props);
    console.log(this.props.list);
  }
  componentDidMount(){
    
  }

  render(){
    return(
      
      <ul className="albums">
        {this.props.list.map(album => (
          <li key={album._id} className="albumlist"><img src={album.images[2].url} width={album.images[2].width} height={album.images[2].height} /> { album.name }</li>
        ))}
      </ul>
    )
  }
} 

export default Playlist;

